import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'


const Common = (props) => {
    return (
        <section id="header" className="d-flex align-items-center">
            <div className="overlay-bg">
            <div className="container-fluid">
                <br/>
                <br/>
                <br/>
                <br/>
                <div className="row">
                    <div className="col-10 mx-auto row">
                        <div className="col-md-6 pt-5 makingBold  d-flex justify-content-center flex-column">
                            <h1>{props.name} <strong className="brand-name">Manella</strong></h1>
                            <hr style={{backgroundColor:"white",fontsize:"100px"}}/>
                            <h4 className="my-3 des-color">{props.description}</h4>
                            <div className="mt-3">
                                <NavLink type="button" to={props.visit} className="btn btn-outline-light">{props.btname}</NavLink>
                            </div>
                        </div>
                        <div className="col-md-6 pt-5 header-img">
                            <img src={props.imgsrc} alt="common img" className="img-fluid animated" />
                        </div>
                    </div>
                </div>
            </div>  
            </div>
        </section>
    )
}

export default Common;


