import React, { Component } from 'react'
import web from "../src/images/img-header.svg";
import Common from './Common'



export default class Home extends Component {
    render() {
        return (
        <div className="home-page">
               <Common
                name='Grow your business with '
                description="We are making websites for everyone around the world"
                imgsrc={web}
                visit="/services"
                btname="Get Started"
            /> 
        </div>
        )  
            
        
    }
}
